<template>
  <div class="classify-seletor" :class="{show:showDataFilterPlane}">
    <div class="mask" @click="hidePlane"></div>
    <div class="wrap">
      <div class="classify-header">选择交易类型</div>
      <div class="classify-group">
        <div class="classify-item" :class="{selected:selectedType==0}" @click="showAll">全部</div>
        <div class="classify-item" :class="{selected:selectedType==1}" @click="showFinderLive">视频号直播</div>
        <div class="classify-item" :class="{selected:selectedType==2}" @click="showGroupLive">群直播</div>
      </div>
      <div class="close" @click="hidePlane">取消</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    selectedType: {
      type: Number,
      default: 0
    },
    showDataFilterPlane: Boolean
  },
  methods: {
    showAll() {
      this.$emit("hidePlane", 0);
    },
    showFinderLive() {
      this.$emit("hidePlane", 1);
    },
    showGroupLive() {
      this.$emit("hidePlane", 2);
    },
    hidePlane() {
      this.$emit("hidePlane");
    }
  }
};
</script>

