<template>
  <div id="app">
    <template v-if="!showDetail">
      <!-- <DataFilter v-if="!isPayPage" v-on:click.native="DataFilterClick" /> -->
      <ItemHead v-if="!isPayPage" />
      <Item
        v-for="item in showListData"
        :type="item.type"
        :title="item.title"
        :date="item.date"
        :money="item.money"
        :desc="item.desc"
        v-bind:key="item.title"
        v-on:click.native="itemClick(item)"
      />
      <DataFilterPlane
        :selectedType="selectedType"
        :showDataFilterPlane="showDataFilterPlane"
        v-on:hidePlane="hidePlane"
      />
    </template>
    <template v-else>
      <Detail
        :type="selectedItem.type"
        :title="selectedItem.title"
        :money="selectedItem.money"
        :date="selectedItem.date"
      />
    </template>
  </div>
</template>

<script>
import Item from "./components/Item.vue";
import ItemHead from "./components/ItemHead.vue";
//import DataFilter from "./components/DataFilter.vue";
import DataFilterPlane from "./components/DataFilterPlane.vue";
import Detail from "./components/Detail.vue";

export default {
  name: "App",
  components: {
    Item,
    ItemHead,
    //DataFilter,
    DataFilterPlane,
    Detail
  },
  data() {
    return {
      isPayPage: false,
      showDetail: false,
      showDataFilterPlane: false,
      selectedType: 0,
      selectedItem: {},
      listData: [],
      showListData: []
    };
  },
  mounted() {
    let me = this;
    window.onpopstate = function() {
      me.showDetail = false;
    };
    me.isPayPage = /pay=1/.test(document.location.search);
    console.log("me.isPayPage", me.isPayPage);
    if (me.isPayPage) {
      me.listData = [
        {
          type: 2,
          title: "群直播打赏-飞吻x1",
          money: "-10.00",
          date: "10月10日 11:24",
          desc: "余额 12.00"
        },
        {
          type: 2,
          title: "视频号直播打赏-喜欢你x1",
          money: "-188.00",
          date: "10月10日 11:22",
          desc: "余额 22.00"
        },
        {
          type: 1,
          title: "充值-微信豆x30",
          money: "+210.00",
          date: "10月10日 10:01",
          desc: "余额 210.00"
        }
      ];
    } else {
      me.listData = [
        {
          type: 1,
          title: "视频号直播-10月10日14:00场次",
          money: "+10.00",
          date: "10月10日 14:24",
          desc: "余额583.05"
        },
        {
          type: 1,
          title: "视频号直播-10月10日11:32场次",
          money: "+53.05",
          date: "10月10日 11:40",
          desc: "余额573.05"
        },
        {
          type: 2,
          title: "提现",
          money: "-103.56",
          date: "10月8日",
          desc: "余额420.00"
        },
        {
          type: 1,
          title: "视频号直播-10月8日00:32场次",
          money: "+523.56",
          date: "10月8日02:00",
          desc: "余额523.56"
        }
      ];
    }
    me.showListData = me.listData;
  },
  methods: {
    itemClick(item) {
      history.pushState({ detail: true }, "");
      this.showDetail = true;
      this.selectedItem = item;
    },
    DataFilterClick() {
      this.showDataFilterPlane = true;
    },
    hidePlane(type) {
      this.showDataFilterPlane = false;
      if (typeof type == "undefined") return;
      this.selectedType = type;
      switch (this.selectedType) {
        case 0:
          this.showListData = this.listData;
          break;
        default:
          this.showListData = this.listData.filter(
            data => data.type == this.selectedType
          );
      }
    }
  }
};
</script>

<style>
@font-face {
  font-family: "WeChatSansStd";
  src: url("https://res.wx.qq.com/a/wx_fed/cdn_libs/res/WeChatSans/1.1.0/WeChatSansStd-Regular.ttf");
}
